/**
 * @ data æ¨¡æ¿å¯¹è±¡
 * @param {string} data.id ä¿¡æ¯æµid
 * @param {string} data.title æ ‡é¢˜
 * @param {string} data.publishTime å‘å¸ƒæ—¶é—´
 * @param {number} data.commentNum è¯„è®ºæ•°
 * @param {string} data.feedType ä¿¡æ¯æµæ¨¡æ¿ç±»å‹
 * @param {array} data.pics å›¾ç‰‡æ•°ç»„
 * @param {string} data.adLink å¹¿å‘Šé“¾æ¥
 * @param {boolean} data.isAd æ˜¯ä¸æ˜¯å¹¿å‘Š
 * @param {boolean} data.isTop æ˜¯ä¸æ˜¯ç½®é¡¶
 * @param {boolean} data.isHot æ˜¯ä¸æ˜¯çƒ­é—¨
 * @param {boolean} data.showHead æ˜¯å¦æ˜¾ç¤ºå¤´åƒ
 * 
 * @ data.author ä½œè€…å¯¹è±¡
 * @param {string} author.id ä½œè€…id
 * @param {string} author.name ä½œè€…åç§°
 * @param {string} author.avatar ä½œè€…å¤´åƒ
 * @param {string} author.label ä½œè€…æ ‡ç­¾
 * @param {boolean} author.vip ä½œè€…æ˜¯å¦æ˜¯vip
 * @param {boolean} author.friend æ˜¯å¦å…³æ³¨äº†ä½œè€…
 * 
 * @ å›è°ƒå‡½æ•°
 * @param {function} onFriend å…³æ³¨æˆ–è€…å–å…³ (isFriend) => {}
 * @param {function} onRemove ç§»é™¤å¹¿å‘Š
**/
class FeedFlow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  preventClick = e => {
    e.stopPropagation();
    e.preventDefault();
  }

  handleRemove = () => {
    this.props.onRemove && this.props.onRemove();
  }

  handleFriend = () => {
    this.props.onFriend && this.props.onFriend(this.props.data.author.friend);
  }

  // æ¸²æŸ“infoéƒ¨åˆ†
  renderInfo = () => {
    const { data } = this.props;
    const { publishTime, commentNum, author, isAd, isTop, isHot, showHead } = data;
    const { name } = author;
    return (
      <div className="feed-info" onClick={this.preventClick}>
          <div className="info-l">
            {isAd && <span className="ad">å¹¿å‘Š</span>}
            {isTop && <span className="red">ç½®é¡¶</span>}
            {isHot && <span className="red">çƒ­é—¨</span>}
            {!showHead && <span>{name}</span>}
            {!isAd && <span>è¯„è®º {commentNum}</span>}
            <span>{publishTime}</span>
          </div>
          <div className="info-r">
            {isAd && <span className="close" onClick={this.handleRemove} />}
          </div>
        </div>
    );
  }

  // æ¸²æŸ“bodyéƒ¨åˆ†
  renderBody = () => {
    const { data } = this.props;
    const { title, feedType, pics, duration } = data;
    const feedBody = {
      smallpic: (
        <>
          <div className="feed-l">
            <div className="feed-tit line3" dangerouslySetInnerHTML={{ __html: title }}></div>
            {this.renderInfo()}
          </div>
          <div className="feed-r">
            <img src={pics[0]} alt=""/>
          </div>
        </>
      ),
      threepic: (
        <>
          <div className="feed-tit line3" dangerouslySetInnerHTML={{ __html: title }}></div>
          {pics.length > 0 && <div className="feed-pic">
            {pics.map((pic, i) => <img key={i} src={pic} alt=""/>)}
          </div>}
          {this.renderInfo()}
        </>
      ),
      bigpic: (
        <>
          <div className="feed-tit line3" dangerouslySetInnerHTML={{ __html: title }}></div>
          {pics[0] && <div className="feed-pic">
            <img src={pics[0]} alt=""/>
          </div>}
          {this.renderInfo()}
        </>
      ),
      feedvideo: (
        <>
          <div className="feed-pic">
            <img src={pics[0]} alt=""/>
            <div className="feed-tit">
              <div className="line2" dangerouslySetInnerHTML={{ __html: title }}></div>
            </div>
            <span className="videoplay" />
            <span className="videotime">{duration}</span>
          </div>
          {this.renderInfo()}
        </>
      )
    };
    return feedBody[feedType];
  }

  render() {
    const { data } = this.props;
    const { feedType, author, isAd, adLink, showHead } = data;
    const { name, avatar, label, vip, friend } = author;
    const linkUrl = {
      href: isAd && adLink,
      to: !isAd && `/${feedType === 'feedvideo' ? 'video' : 'p'}/${data.id}`
    };

    return (
      <SmartLink className={`feed feed-${feedType}`} {...linkUrl}>
        {showHead && <div className="feed-hd" onClick={this.preventClick}>
          <div className="author-avatar">
            <img src={avatar} alt=""/>
            {vip && <span className="vip" />}
          </div>
          <div className="author-info">
            <div className="name">{name}</div>
            <div className="label">{label}</div>
          </div>
          <div className="author-friend">
            <span
              className={friend ? 'friend' : 'unfriend'}
              onClick={this.handleFriend}
            >{friend ? 'å·²å…³æ³¨' : 'å…³æ³¨'}</span>
          </div>
        </div>}
        {this.renderBody()}
      </SmartLink>
    );
  }
}

export default FeedFlow;
