import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import NotFound from 'components/NotFound';

const req = require.context('../pages', true, /route\.jsx?$/);
const allRoutes = [].concat.apply([], req.keys().map(k => req(k).default || []));

/**
 * åŠŸèƒ½ï¼šè·å–æŸä¸ªçˆ¶è·¯ç”±ä¸‹çš„æ‰€æœ‰å­è·¯ç”±
 * @param {string} fatherPath çˆ¶è·¯ç”±
 */
const getRoutes = (fatherPath) => {

  //é€‰å‡ºéœ€è¦æ¸²æŸ“çš„è·¯ç”±
  const routes = allRoutes.filter(v => v.fatherPath === fatherPath);

  //ç»™æ¯ä¸€å±‚è·¯ç”±éƒ½æ·»åŠ 404
  routes.push({ exact: false, path: fatherPath === '/' ? '/*' : fatherPath + '/*', component: NotFound });

  return (
    <Switch>
      {routes.map(route => {
        //é‡å®šå‘è·¯ç”±
        if (route.redirect) {
          return <Route
            key={route.path}
            exact={route.exact}
            path={route.path}
            render={() => <Redirect to={route.redirect} />}
          />
        } 
        //ä¸€èˆ¬è·¯ç”±
        return <Route
          key={route.path}
          exact={route.exact}
          path={route.path}
          component={route.component}
        />
      })}
    </Switch>
  );
}

export default getRoutes;
