import React from 'react';
import { createPortal } from 'react-dom';
import './index.less';

function alertFn(config, isConfirm) {
  // é¿å…æ¶æ„çš„é‡å¤åˆ›å»º
  if (document.querySelector('.alert-root')) return;
  
  if (!config) {
    config = { content: config === 0 ? '0' : '' };
  } else if (typeof config !== 'object') {
    config = { content: String(config) };
  }

  const alertRoot = document.createElement('div');
  alertRoot.className = 'alert-root';
  alertRoot.innerHTML = `
    <div class="alert-mask show">
      <div class="alert-site">
        <div class="alert">
          <div class="alert-content">${config.content}</div>
          <div class="alert-btns">
            ${isConfirm ? `<span class="btn-cancel">${config.cancelText || 'å–æ¶ˆ'}</span>` : ''}
            <span class="btn-ok">${config.okText || 'ç¡®å®š'}</span>
          </div>
        </div>
      </div>
    </div>`;
  document.body.appendChild(alertRoot);
  const btnOk = document.querySelector('.alert-root .btn-ok');
  btnOk.onclick = () => {
    config.onOk && config.onOk();
    document.querySelector('.alert-root .alert-mask').className = 'alert-mask hiding';
    setTimeout(() => {
      document.body.removeChild(alertRoot);
    }, 300);
  }
  if (isConfirm) {
    const cancelOk = document.querySelector('.alert-root .btn-cancel');
    cancelOk.onclick = () => {
      config.onCancel && config.onCancel();
      document.querySelector('.alert-root .alert-mask').className = 'alert-mask hiding';
      setTimeout(() => {
        document.body.removeChild(alertRoot);
      }, 300);
    }
  }
}

/**
 * @param {boolean} visible æ§åˆ¶æ¨¡æ€æ¡†æ˜¯å¦æ˜¾ç¤º
 * @param {string} direction æ¨¡æ€æ¡†å‡ºç°çš„æ–¹å‘ï¼Œæ”¯æŒ3ä¸ªå€¼ï¼šleftã€rightã€bottomï¼Œé»˜è®¤left
 * @static Modalæ”¯æŒä¸¤ä¸ªé™æ€æ–¹æ³•ï¼šModal.alert(config)å’ŒModal.confirm(config)ï¼Œç”¨äºæ¨¡æ‹ŸåŸç”Ÿçš„alertå’Œconfirmã€‚
 * @description configå¯ä»¥æ˜¯ä¸€ä¸ªå­—ç¬¦ä¸²æˆ–è€…ä¸€ä¸ªé…ç½®å¯¹è±¡ã€‚configå¦‚æœæ˜¯ä¸ªå¯¹è±¡ï¼Œå…¶å…¨éƒ¨å‚æ•°å¦‚ä¸‹
 ```js
  {
    content: 'æç¤ºå†…å®¹',
    okText: 'è‡ªå®šä¹‰okæŒ‰é’®æ–‡å­—ï¼Œé»˜è®¤â€œç¡®å®šâ€',
    cancelText: 'è‡ªå®šä¹‰cancelæŒ‰é’®æ–‡å­—ï¼Œé»˜è®¤â€œå–æ¶ˆâ€',
    onOk: () => {//ç‚¹å‡»okæŒ‰é’®è§¦å‘},
    onCancel: () => {//ç‚¹å‡»cancelæŒ‰é’®è§¦å‘}
  }
 ```
 * 
*/
class Modal extends React.Component {
  constructor(props) {
    super(props);
    const oldModalRoot = document.querySelector('.modal-root');
    this.modalRoot = oldModalRoot || document.createElement('div');
    if (!oldModalRoot) {
      this.modalRoot.className = 'modal-root';
      document.body.appendChild(this.modalRoot);
    }

    this.state = {
      isExiting: false
    }
  }

  static alert = (config) => {
    alertFn(config, false);
  }

  static confirm = (config) => {
    alertFn(config, true);
  }

  enterClass = {
    left: 'enter-left',
    right: 'enter-right',
    bottom: 'enter-bottom'
  }

  exitClass = {
    left: 'exit-left',
    right: 'exit-right',
    bottom: 'exit-bottom'
  }

  componentDidUpdate(preProps) {
    if (preProps.visible && !this.props.visible) {
      this.setState({ isExiting: true });
      this.slideTimer = setTimeout(() => {
        this.setState({ isExiting: false });
      }, 290);
    } else if (!preProps.visible && this.props.visible) {
      clearTimeout(this.slideTimer);
      this.slideTimer = null;
      this.setState({ isExiting: false });
    }
  }

  render() {
    const { visible, direction = 'left' } = this.props;
    const { isExiting } = this.state;

    if (!visible && !isExiting) return null;

    const maskAniCls = isExiting ? this.exitClass[direction] : this.enterClass[direction];

    return createPortal(
      <div className={`modal-mask ${maskAniCls}`}>
        <div className="modal-site">
          {this.props.children}
        </div>
      </div>,
      this.modalRoot
    );
  }
}

export default Modal;
