import React from 'react';

let store = {};

/**
 * @param {string} key è¦å–å“ªä¸ªæ•°æ®/å–å…¨éƒ¨
*/
export const getStore = (key) => key ? store[key] : store;

/**
 * @param {object} state è®¾ç½®å“ªäº›æ•°æ®
*/
export const setStore = (state = {}) => store = {...store, ...state};

/**
 * @param {array} keys è®¢é˜…å“ªäº›æ•°æ®
 * @param {component} Component ç»‘å®šçš„ç»„ä»¶
*/
export const connect = (keys, Component) => {

  return class Provider extends React.Component {
    constructor(props) {
      super(props);
      const initState = {};
      keys.forEach(key => {
        initState[key] = store[key];
      });
      this.state = initState
    }
  
    dispatch = (state = {}) => {
      store = {...store, ...state};
      this.setState(state);
    }
  
    render() {
      return <Component dispatch={this.dispatch} {...this.state} {...this.props} />;
    }
  }
}